import React from "react";
import styled from "styled-components";

import { StyledFlexBox } from "../FlexBox";

const StyledHeaderLayout = styled(StyledFlexBox).attrs({ as: "header" })`
  width: 100px;
  height: 100px;
  background-color: #555;
`;

export default function HeaderLayout(): JSX.Element {
  return (
    <>
      <FlexBox />
      <StyledHeaderLayout />
    </>
  );
}
